<template>
  <v-list-tile :class="{'grey lighten-4': item.isEnrolled}">
    <v-list-tile-action v-if="item.isEnrolled">
      <v-icon :color="item.records[0].slice(0, 5) === '10710' ? '' : 'teal'">check</v-icon>
    </v-list-tile-action>
    <v-list-tile-content>
      <v-list-tile-title>{{ item.name }}</v-list-tile-title>
    </v-list-tile-content>
    <v-list-tile-action v-if="settings.includes('') && item.records.length">
      <v-tooltip v-if="new Set(item.records.map(v => v.slice(5, 9))).size > 1" bottom>
        <v-chip color="primary" slot="activator" outline disabled small>
          {{ `${record2dept(item.records[0])}` }}
          <v-icon small right class="ml-1">info</v-icon>
        </v-chip>
        <span v-html="depts"/>
      </v-tooltip>
      <v-chip v-else color="primary" outline disabled small>
        {{ `${record2dept(item.records[0])}` }}
      </v-chip>
    </v-list-tile-action>
    <v-list-tile-action v-if="settings.includes('') && item.records.length">
      <v-tooltip v-if="new Set(item.records.map(v => v.slice(0, 5))).size > 1" bottom>
        <v-chip color="primary" slot="activator" outline disabled small>
          {{ `${record2time(item.records[0])}` }}
          <v-icon small right class="ml-1">info</v-icon>
        </v-chip>
        <span v-html="ordinalYear"/>
      </v-tooltip>
      <v-chip v-else color="primary" outline disabled small>
        {{ `${record2time(item.records[0])}` }}
      </v-chip>
    </v-list-tile-action>
    <v-list-tile-action v-if="settings.includes('') && item.credits.length">
      <v-tooltip v-if="new Set(item.credits).size > 1" bottom>
        <v-chip color="primary" text-color="white" slot="activator" small>
          {{ `${item.credits[0]} ` }}
          <v-icon small right class="ml-1">info</v-icon>
        </v-chip>
        <span v-html="creditSemesters"/>
      </v-tooltip>
      <v-chip v-else color="primary" text-color="white" small>
        {{ `${item.credits[0]} ` }}
      </v-chip>
    </v-list-tile-action>
    <v-list-tile-action v-if="item.credits.length === 0">
      <v-chip color="error" outline disabled small></v-chip>
    </v-list-tile-action>
  </v-list-tile>
</template>

<script>
